@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.hero-enter{opacity:0}.hero-enter-active{opacity:1;transition:opacity 500ms ease-in}.hero-exit{opacity:1}.hero-exit-active{opacity:0;transition:opacity 500ms ease-in}
.app{height:100vh;overflow-y:scroll;background:url(/heroes/static/media/hero_bg.0540b794.jpg) center/cover no-repeat}.content{display:grid;grid-template-columns:560px 440px;max-width:1040px;margin:0 auto;padding-top:50px}.content__interactive{padding-left:40px}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}ul{padding:0}
